import React, { useState } from "react";
import "../styles/navbar.scss";
import logo from "../img/logo1.png";
import { CgMenuRight as CloseMenu } from "react-icons/cg";
import { CgMenu as MenuIcon } from "react-icons/cg";
import { SiDiscord as DiscordIcon } from "react-icons/si";
import { SiGithub as GitHubIcon } from "react-icons/si";
function Navbar() {
  const [click, setClick] = useState(false);
  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);
  return (
    <nav className="navbar">
      <div className="container nav-flex">
        <div className="nav-brand" href="#1">
          <img src={logo} alt="" />
          <a href="#1">MegaDev</a>
        </div>
        <div className="nav-content">
          <ul
            className={click ? "nav-items active" : "nav-items"}
            onClick={handleClick}
          >
            <li className="nav-item" onClick={closeMobileMenu}>
              <a
                className="nav-link"
                aria-current="page"
                href="https://discord.gg/NRtaCtEQpe"
                target="new"
              >
                JoinUs
              </a>
            </li>
            <li className="nav-item" onClick={closeMobileMenu}>
              <a className="nav-link" href="#1">
                about
              </a>
            </li>
            <li className="nav-item" onClick={closeMobileMenu}>
              <a className="nav-link" href="#1">
                contact
              </a>
            </li>
            <li className="nav-item" onClick={closeMobileMenu}>
              <a
                className="nav-link"
                target="new"
                href="https://discord.gg/NRtaCtEQpe"
              >
                <DiscordIcon className="nav-icon" />
              </a>
            </li>
            <li className="nav-item" onClick={closeMobileMenu}>
              <a
                className="nav-link"
                target="new"
                href="https://github.com/saifmohamedo"
              >
                <GitHubIcon className="nav-icon" />
              </a>
            </li>
          </ul>
        </div>
      </div>
      <div className="mobile-menu" onClick={handleClick}>
        {click ? (
          <CloseMenu className="menu-icon" />
        ) : (
          <MenuIcon className="menu-icon" />
        )}
      </div>
    </nav>
  );
}

export default Navbar;
